


















import Vue from "vue";
import Component from "vue-class-component";
import { contact } from "@/scripts/contact";

class UserInput {
  public name: string = "";
  public pubKey: string = "";
}

@Component({})
export default class AddFriend extends Vue {
  private userInput: UserInput = new UserInput();
  private visible: boolean = false;

  private get pass(): boolean {
    return this.userInput.name !== "" && this.userInput.pubKey !== "";
  }

  private show() {
    this.visible = true;
  }

  private hide() {
    this.reset();
    this.visible = false;
  }

  private reset() {
    const form: any = this.$refs.form;
    form.resetFields();
  }

  private sure() {
    if (this.userInput.name.length > 6) {
      this.$message({ message: "名字太长了啊(6个字符)!", type: "warning" });
      return;
    }

    const priv = new contact.Private(
      this.userInput.pubKey,
      this.userInput.name,
      this.userInput.pubKey
    );

    this.$store.commit("addPrivate", priv);
    this.hide();
  }
}
